<template>
  <div style="width: 100vw;overflow: hidden;">
    <van-sticky>
      <div class="topTab">
        <van-icon name="arrow-left" size="20" @click="$router.back()" />
        <div class="title">买家秀详情</div>
      </div>
      <div class="productInfo" v-if="shopDetails?.products[0]">
        <img :src="shopDetails?.products[0]?.img" alt="" />
        <div>{{ shopDetails?.products[0]?.name }}</div>
      </div>
    </van-sticky>
    <div class="user">
      <div class="flex">
        <img :src="shopDetails?.icon" alt="" />
        <div class="nickname">
          {{ shopDetails?.nickname }}
          <div class="day">{{ shopDetails?.create_time }}</div>
        </div>
        <div class="btn">关注</div>
      </div>
    </div>
    <div class="content">
      {{ shopDetails?.content }}
      <div>
        <img v-for="(item, index) in shopDetails?.pic_list" :key="index" :src="item.img" alt="" />
      </div>
    </div>
    <div class="comment">
      <p>共{{ shopComment?.length }}条评论</p>
      <div class="say">
        <img :src="shopDetails?.cur_user_info.avatar" alt="" />
        <input type="text" placeholder="来说点什么..." @click="say" />
      </div>
      <div class="user user-comment" v-for="(item, index) in shopComment" :key="index">
        <div class="flex" style="margin: 8px">
          <img :src="item?.img" alt="" />
          <div>
            <div class="nickname">
              {{ item?.nickname }}
              <div class="day">{{ item?.create_time }}</div>
            </div>
            <div>{{ item.comment }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      shopDetails: null,
      shopComment: null,
    };
  },
  methods: {
    getDetail() {
      this.axios
        .get(
          `communicate/mizone_product_comment_detail?comment_id=${this.$route.query.id}`
        )
        .then((res) => {
          // console.log(res.data.data);
          this.shopDetails = res.data.data;
        });
    },
    getComment() {
      this.axios
        .get(
          `communicate/mizone_comment_list?comment_type=product_comment&product_comment_id=${this.$route.query.id}&page_size=10&page_index=1`
        )
        .then((res) => {
          // console.log(res.data.data.comment_list);
          this.shopComment = res.data.data?.comment_list;
        });
    },
    say() {
      Toast("请前往app参与");
    },
  },
  created() {
    this.getComment();
    this.getDetail()
  },
};
</script>

<style lang="less" scoped>
.topTab {
  padding: 0 0.32rem;
  height: 0.88rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(242, 242, 242);

  .title {
    width: 90vw;
    text-align: center;
  }
}

.productInfo {
  font-size: 0.24rem;
  color: #000;
  line-height: 0.28rem;
  display: flex;
  align-items: center;
  height: 0.8rem;
  background: #fafafa;
  padding: 0 0.32rem;

  img {
    width: 0.43rem;
    height: 0.57rem;
    margin-right: 0.3rem;
  }
}

.user {
  width: 100vw;
  margin-bottom: 0.2rem;
  display: flex;
  align-items: center;
  padding-top: 0.3rem;

  .flex {
    display: flex;
    margin: auto;
  }

  img {
    margin-right: 0.16rem;
    width: 0.52rem;
    height: 0.52rem;
    border-radius: 0.52rem;
    overflow: hidden;
  }

  .nickname {
    font-size: 0.24rem;
    font-weight: 500;
    color: #161616;
    line-height: 1.4;
    width: 64vw;
  }

  .day {
    font-size: 0.2rem;
    font-weight: 500;
    color: #757575;
  }

  .btn {
    padding: 0 0.33rem;
    height: 0.48rem;
    line-height: 0.48rem;
    background: #fff;
    border-radius: 0.32rem;
    border: 1px solid #ff5528;
    font-size: 0.24rem;
    font-weight: 500;
    text-align: center;
    color: #ff5528;
  }
}

.user-comment{
  border-bottom: .1px solid #f2f2f2;
  padding-bottom: 4px;
}

.content {
  padding: 0 0.24rem;
  overflow: hidden;
  margin-top: 0.18rem;
  font-size: 0.3rem;
  color: #333;
  line-height: 1.8;
  word-wrap: break-word;

  img {
    margin-top: 0.2rem;
    border-radius: 0.14rem;
    width: 2.1rem;
    height: 2.44rem;
    object-fit: cover;
  }
}

.comment {
  width: 96%;
  margin: auto;

  p {
    margin-bottom: 0.28rem;
    color: #333;
    font-size: 0.24rem;
  }

  .say {
    display: flex;
    align-items: center;

    img {
      margin-right: 0.24rem;
      width: 0.52rem;
      height: 0.52rem;
      border-radius: 50%;
      overflow: hidden;
    }

    input {
      width: 80vw;
      padding: 8px 16px;
      background: #00000014;
      border-radius: 20px;
      border: none;

      &::placeholder {
        color: #b4b4b4;
        font-size: 0.25rem;
      }
    }
  }
}
</style>